.about{
    display: flex;
    height: fit-content;
}
#abtimg{
    width: 50%;
}
.cnt{
    background-color: rgb(159, 248, 239);
    text-align: center;
    margin: 5px;
    margin-bottom: 10px;
    border-radius: 10px;
    padding: 30px;
}
.cnt:hover{
    background-color: rgb(108, 255, 240);
}
.cont{
    display: block;
    width: 50%;
    
}
.contact{
    display: flex;
}
.imggal{
    text-align: center;
}

@media only screen and (max-width: 600px) {
    #abtimg{
        width: 0%;
    }
    .about{
        display: block;
    }
    .cont{
        width: 100%;
    }
    #map{
        width: 100%;
        height: 850;
    }
    .cnt{
        background-color: rgb(159, 248, 239);
        text-align: center;
        width: unset;
        margin: 5px;
        border-radius: 10px;
        padding: 30px;
    }
    .contact{
        display: block;
    }
    
}