.category{
    
    display:flex;
    gap: 10px;
    width: 100%;
  
    padding: 10px;
}
#subcat{
    width: 100%;
    /* padding: 20px; */
   text-align: center;
   display: block;
  background-color: #faeeca;
  color: #979494;
   
   
}
#subcat:hover{
    background-color:#ffc107;
    color: #161616; 
    
}
#catimg{
    width: 80px;
    border-radius: 40px;
    border: black solid;
    padding: 1px;
}
#cattitle{
    padding: 0%;
    margin: 0%;
}
#desc{
    padding: 0%;
    margin: 0%;
}
#buy{
    margin: 2px 10px;
    padding: 2px 10px ;
    border-radius: 20px;
    left: 0%;
}

@media only screen and (max-width: 600px) {
    #catimg{
        width: 30px;
        border-radius: 30px;
    }
    #subcat{
       width: min-content;       
    }
    .category{
       
        display:flex;
        gap: 5px;
        width: 100%;
        height: fit-content;
        padding: 5px;
        overflow-x: scroll;
        scroll-behavior: smooth;
    }
    #catcard{
        display:flex;
        flex-direction: row;
        gap: 10px;
        padding: 10px;
        width: 100%;
        margin: 0%;
        height: fit-content;
        
    }
    #cont{
        width: 100%;
         }
    .img{
        width: 90px;
      
    }
    #catimage{  height: 100px;
        width: 90px;
        left: 0%;
        margin-top: 10px;
        margin-left: 10px;

    }
    #catbody{
        width: 90px;
        height: fit-content;
        
    }
    .con{
        padding:2% ;
        text-align: left;
    }
    #cattitle,#desc{
        text-align: left;
        font-weight: 600;
    }
}