#navbar-brand {
    display: inline-block;
    padding-top: 0.3125rem;
    padding-bottom: 0.3125rem;
    margin: 0rem 4rem;
    font-size: 1.3rem;
    line-height: inherit;
    white-space: nowrap;
    font-weight:600;
}
@media only screen and (max-width: 600px) {
    #navbar-brand {
        display: inline-block;
        padding-top: 0.3125rem;
        padding-bottom: 0.3125rem;
        margin:0px;
        font-size: 1.1rem;
        line-height: inherit;
        white-space: nowrap;
        font-weight:600;
    }   
}